// CircleBold
@font-face {
  font-family: 'CircleBold';
  src: url('../fonts/circle_bold/Circe-Bold.eot');
  src: url('../fonts/circle_bold/Circe-Bold.eot?#iefix') format('embedded-opentype'),
       url('../fonts/circle_bold/Circe-Bold.ttf') format('truetype'),
       url('../fonts/circle_bold/Circe-Bold.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  text-rendering: optimizeLegibility;
}

//CircleLight
@font-face {
  font-family: 'CircleLight';
